<template>
    <v-card color="primary" class="ma-10 rounded-xl" elevation="10">
        <v-card-title class="secondary--text">
            <v-icon color="secondary" class="mr-5" v-text="icon" />
            {{name}}
            <v-spacer></v-spacer>
            <v-icon 
                v-if="!hide_circle && circle_color!=null"
                :color="circle_color"
            >
                fas fa-circle
            </v-icon>
        </v-card-title>

        <v-card-text class="secondary--text">
            <v-row justify="center" class="ma-0" align="center">
                <p class="text-h2 mr-3"><b>{{value==null?'-':Math.round(value * 100) / 100}}</b></p>
                {{unit}}
            </v-row>
        </v-card-text>

        <v-btn 
            icon
            color="white"
            absolute
            bottom
            right
            @click="open_parameter_details_dialog = true"
            v-if="!hide_search"
        >
            <v-icon>fas fa-search</v-icon>
        </v-btn>

        <v-dialog v-model="open_parameter_details_dialog">
            <ParameterDetails 
                :type="show_type" 
                v-on:close="open_parameter_details_dialog = false" 
                :reservoir="reservoir" 
                v-if="open_parameter_details_dialog"
                v-on:next="next"
                v-on:back="back"
            />
        </v-dialog>
    </v-card>
</template>

<script>
import ParameterDetails from '../components/ParameterDetails'

export default {
    data: () => ({
        name: '',
        unit: '',
        circle_color: '',
        icon: null,
        open_parameter_details_dialog: false,
        show_type: null
    }),
    props: {
        type: String,
        value: Number,
        hide_circle: Boolean,
        reservoir: Number,
        hide_search: Boolean
    },
    components: {
        ParameterDetails
    },
    watch: {
        open_parameter_details_dialog(){
            if(this.open_parameter_details_dialog == false){
                this.show_type = this.type
            }
        }
    },
    methods: {
        next(type){
            if(type == 'wqi'){
                this.show_type = 'temp'
            }
            if(type == 'temp'){
                this.show_type = 'ph'
            }
            if(type == 'ph'){
                this.show_type = 'do'
            }
            if(type == 'do'){
                this.show_type = 'ec'
            }
            if(type == 'ec'){
                this.show_type = 'tds'
            }
            if(type == 'tds'){
                this.show_type = 'sal'
            }
            if(type == 'sal'){
                this.show_type = 'turb'
            }
            if(type == 'turb'){
                this.show_type = 'vol'
            }
        },
        back(type){
            if(type == 'temp'){
                this.show_type = 'wqi'
            }
            if(type == 'ph'){
                this.show_type = 'temp'
            }
            if(type == 'do'){
                this.show_type = 'ph'
            }
            if(type == 'ec'){
                this.show_type = 'do'
            }
            if(type == 'tds'){
                this.show_type = 'ec'
            }
            if(type == 'sal'){
                this.show_type = 'tds'
            }
            if(type == 'turb'){
                this.show_type = 'sal'
            }
            if(type == 'vol'){
                this.show_type = 'turb'
            }
        }
    },
    created(){
        this.show_type = this.type
    },
    mounted(){
        if(this.type == 'wqi'){
            this.name = 'IQA'
            
            if (this.value <= 25) {
                this.circle_color = 'green'
            } 
            else if (this.value <= 50) {
                this.circle_color = 'green'
            } 
            else if (this.value <= 75) {
                this.circle_color = 'yellow'
            } else if (this.value <= 100) {
                this.circle_color = 'red'
            } else {
                this.circle_color = 'red'
            }
        }
        else if(this.type == 'temp'){
            this.name = 'Temperatura'
            this.unit = 'ºC'

            if (this.value >= 0 && this.value <= 27) this.circle_color = 'green'
            else if (this.value <= 30) this.circle_color = 'yellow'
            else this.circle_color = 'red'
        }
        else if(this.type == 'ph'){
            this.name = 'pH'
            this.unit = 'un'

            if (this.value >= 6.5 && this.value <= 8.5) this.circle_color = 'green'
            else if ((this.value >= 6 && this.value < 6.5) || (this.value > 8.5 && this.value < 9.0)) this.circle_color = 'yellow'
            else this.circle_color = 'red'
        }
        else if(this.type == 'do'){
            this.name = 'Oxigênio Dissolvido'
            this.unit = 'mg/l'

            if (this.value > 5.5) this.circle_color = 'green'
            else if (this.value >= 5) this.circle_color = 'yellow'
            else this.circle_color = 'red'
        }
        else if(this.type == 'ec'){
            this.name = 'Condutividade Elétrica'
            this.unit = 'µS/cm'

            if (this.value < 95) this.circle_color = 'green'
            else if (this.value <= 100) this.circle_color = 'yellow'
            else this.circle_color = 'red'
        }
        else if(this.type == 'tds'){
            this.name = 'T.D.S.'
            this.unit = 'ppm'

            if (this.value < 450) this.circle_color = 'green'
            else if (this.value <= 500) this.circle_color = 'yellow'
            else this.circle_color = 'red'
        }
        else if(this.type == 'sal'){
            this.name = 'Salinidade'
            this.unit = 'PSU'

            if (this.value < 0.6) this.circle_color = 'green'
            else if (this.value <= 1) this.circle_color = 'yellow'
            else this.circle_color = 'red'
        }
        else if(this.type == 'turb'){
            this.name = 'Turbidez'
            this.unit = 'UNT'

            if (this.value < 4) this.circle_color = 'green'
            else if (this.value <= 5) this.circle_color = 'yellow'
            else this.circle_color = 'red'
        }
        else if(this.type == 'vol'){
            this.name = 'Volume'
            this.unit = 'Litros'

            if (this.value > 200) this.circle_color = 'green'
            else if (this.value > 100) this.circle_color = 'yellow'
            else this.circle_color = 'red'

            this.icon = 'fas fa-water'
        }
        else if(this.type == 'cons'){
            this.name = 'Consumo'
            this.unit = 'Litros/Dia'

            this.circle_color = null

            this.icon = 'fas fa-hand-holding-water'
        }
        else if(this.type == 'pred'){
            this.name = 'Predição'
            this.unit = 'Dia(s)'

            this.circle_color = null

            this.icon = 'fas fa-chart-bar'
        }
    }
}
</script>