<template>
    <v-container fill-height fluid class="primary">
        <v-card class="mx-auto pa-10 rounded-xl" height="500" width="500">
            <v-row justify="center">
                <v-img
                    src='../assets/logo.png'
                    contain
                    max-width="200"
                    max-height="200"
                >
                </v-img>
            </v-row>

            <v-form
                ref="form"
                lazy-validation
                @keyup.native.enter="signin(username, password)"
            >   
                <v-text-field
                    v-model="username"
                    label="E-mail"
                    :rules="[email_rule]"
                ></v-text-field>

                <v-text-field
                    v-model="password"
                    label="Senha"
                    type="password"
                    :rules="[non_empty_rule]"
                ></v-text-field>

                <v-row justify="end">
                    <a @click="forgot_password">Esqueceu a senha?</a>
                </v-row>

                <v-row class="mt-10" justify="center">
                    <v-btn
                        color="primary secondary--text px-15"
                        rounded
                        @click="signin(username, password)"
                    >
                        Entrar
                    </v-btn>
                </v-row>
            </v-form>
        </v-card>
    </v-container>
</template>

<script>
import FormUtils from '../utils/FormUtils'
import {mapState} from 'vuex'

export default {
    data: () => ({
        username: '',
        password: ''
    }),
    computed: {
        ...mapState('auth', ['signin_success'])
    },
    watch: {
        signin_success(){
            if(this.signin_success){
                this.$router.push('/')
            }
        }
    },
    methods: {
        non_empty_rule: FormUtils.non_empty_rule,
        email_rule: FormUtils.email_rule,
        signin(username, password){
            if(this.$refs.form.validate()){
                this.$store.dispatch('auth/signin', { username, password })
            }
        },
        forgot_password(){
            this.$router.push({ name: 'ForgotPassword'})
        }
    }
}
</script>