<template>
    <v-card height="calc(100vh - 175px)" flat tile class="scroller">
        <v-btn
            fixed
            text
            fab
            @click="$emit('back')"
        >
            <v-icon color="primary">fas fa-arrow-left</v-icon>
        </v-btn>

        <v-row class="ma-0">
            <v-col v-for="parameter in parameters" :key="parameter.name" cols="4">
                <ParameterCard 
                    :type="parameter.type" 
                    :value="parameter.val" 
                    :hide_circle="parameter.type=='vol'?true:false"
                    :reservoir="reservoir" />
            </v-col>
        </v-row>
    </v-card>
</template>

<script>
import ParameterCard from '../components/ParameterCard'
import utils from '../utils/utils'
import {mapActions, mapState} from 'vuex'

export default {
    data: () => ({
        parameters: []
    }),
    props: {
        reservoir: Number
    },
    computed: {
        ...mapState('data', ['last_parameters'])
    },
    components: {
        ParameterCard
    },
    watch: {
        reservoir(){
            this.get_last_parameters(this.reservoir)
        },
        last_parameters(){
            this.parameters = [
                {
                    type: 'wqi',
                    val: this.last_parameters['wqi'],
                    //icon: 'fas fa-signal',
                    //icon_color: this.parameters_tolerance('wqi', 25)
                },
                {
                    type: 'temp',
                    val: this.last_parameters['temp'],
                    //icon: 'fas fa-thermometer-half',
                    //icon_color: this.parameters_tolerance('temp', 23)
                },
                {
                    type: 'ph',
                    val: this.last_parameters['pH'],
                    //icon: 'fas fa-flask',
                    //icon_color: this.parameters_tolerance('ph', 7)
                },
                {
                    type: 'do',
                    val: this.last_parameters['OD'],
                    //icon: 'fas fa-lungs',
                    //icon_color: this.parameters_tolerance('do', 32)
                },
                {
                    type: 'ec',
                    val: this.last_parameters['EC'],
                    //icon: 'fas fa-bolt',
                    //icon_color: this.parameters_tolerance('ec', 47)
                },
                {
                    type: 'tds',
                    val: this.last_parameters['TDS'],
                    //icon: 'fas fa-mountain',
                    //icon_color: this.parameters_tolerance('tds', 509)
                },
                {
                    type: 'sal',
                    val: this.last_parameters['salinity'],
                    //icon: 'fas fa-umbrella-beach',
                    //icon_color: this.parameters_tolerance('sal', 9980)
                },
                {
                    type: 'turb',
                    val: this.last_parameters['turb'],
                    //icon: 'fas fa-fill-drip',
                    //icon_color: this.parameters_tolerance('turb', 0)
                },
                {
                    type: 'vol',
                    val: this.last_parameters['volume'],
                    //icon: 'fas fa-water',
                    //icon_color: this.parameters_tolerance('vol', 1316)
                },
            ]
        }
    },
    methods: {
        ...mapActions('data', ['get_last_parameters']),
        parameters_tolerance: utils.parameters_tolerance
    },
    mounted(){
        this.get_last_parameters(this.reservoir)
    }
}
</script>