<template>
    <v-card flat tile>
        <line-chart v-if="show_graph" ref="chart" :chart-data="graph_data" :options="options" />

        <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn text @click="reset_zoom">Reset</v-btn>
        </v-card-actions>
    </v-card>
</template>

<script>
import LineChart from './LineChart.js'
import zoom from 'chartjs-plugin-zoom';
import {mapActions, mapState} from 'vuex'

export default {
    data: () => ({
        show_graph: true,
        data: {
            datasets: []
        },
        graph_data: {},
        options: {
            responsive: true,
            maintainAspectRatio: false,
            scales: {
                xAxes: [{
                    type: 'time',
                    //distribution: 'series',
                    distribution: 'linear',
                    time: {
                        unit: 'hour'
                    }
                }]
            },
            plugins: {
                zoom: {
                    zoom: {
                        enabled: true,
                        drag: true,
                        mode: 'xy'
                    }
                }
            }
        }
    }),
    props: {
        type: String,
        reservoir: Number
    },
    computed: {
        ...mapState('data', ['value_measurements'])
    },
    components: {
        LineChart
    },
    watch: {
        value_measurements(){
            const value_measurements_aux = JSON.parse(JSON.stringify(this.value_measurements));

            value_measurements_aux.sort((firstEl, secondEl) => {
                return new Date(this.convert_date(firstEl.time)) - new Date(this.convert_date(secondEl.time));
            })

            var weekday = new Array(7);
            weekday[0] = "Segunda";
            weekday[1] = "Terça";
            weekday[2] = "Quarta";
            weekday[3] = "Quinta";
            weekday[4] = "Sexta";
            weekday[5] = "Sábado";
            weekday[6] = "Domingo";

            this.data.labels = value_measurements_aux.map((value_measurement) => {
                return this.convert_date(value_measurement.time);
            })

            this.graph_data = JSON.parse(JSON.stringify(this.data))

            this.data.datasets[0].data = value_measurements_aux.map((value_measurement) => value_measurement.value);

            this.graph_data = JSON.parse(JSON.stringify(this.data))
        },
        reservoir(){
            this.get_all_measurements({'reservoir_id': this.reservoir, 'type': this.type})
        },
        type(){
            this.update_data()
        }
    },
    methods: {
        ...mapActions('data', ['get_all_measurements']),
        convert_date(date_time){
            let time = date_time.split(' ')[1];
            let date = date_time.split(' ')[0];
            let [day, month, year] = date.split('/');

            return year+'-'+month+'-'+day+'T'+time;
        },
        reset_zoom(){
            this.show_graph = false
            
            setTimeout(() => this.show_graph = true, 1);
        },
        update_data(){
            this.data.datasets[0] = {backgroundColor: '#390360'}
            this.data.datasets[0].fill = 1

            if(this.type == 'wqi')          this.data.datasets[0].label = 'IQA'
            else if(this.type == 'temp')    this.data.datasets[0].label = 'Temperatura'
            else if(this.type == 'ph')      this.data.datasets[0].label = 'pH'
            else if(this.type == 'do')      this.data.datasets[0].label = 'Oxigênio Dissolvido'
            else if(this.type == 'ec')      this.data.datasets[0].label = 'Condutividade Elétrica'
            else if(this.type == 'tds')     this.data.datasets[0].label = 'Totais de Sólidos Dissolvidos'
            else if(this.type == 'sal')     this.data.datasets[0].label = 'Salinidade'
            else if(this.type == 'turb')    this.data.datasets[0].label = 'Turbidez'
            else if(this.type == 'vol')     this.data.datasets[0].label = 'Volume'
            else if(this.type == 'cons')    this.data.datasets[0].label = 'Consumo'

            this.get_all_measurements({'reservoir_id': this.reservoir, 'type': this.type})
        }
    },
    mounted(){
        this.update_data()
    }
}
</script>