<template>
    <v-snackbar v-model="notificate" top :color="notification_type" class="headline">
        <v-icon v-if="notification_type == 'error' || notification_type == 'warning'" class="mr-2">
            fas fa-exclamation-triangle
        </v-icon>
        <v-icon v-else class="mr-2">fas fa-check-circle</v-icon>

        {{this.message}}

        <template v-slot:action="{ attrs }">
            <v-spacer></v-spacer>
            <v-btn
                color="white"
                icon
                v-bind="attrs"
                @click="notificate = false"
            >
                <v-icon small>fa fa-times</v-icon>
            </v-btn>
        </template>
    </v-snackbar>
</template>

<script>
import {mapState} from 'vuex'

export default {
    data: () => ({
        notificate: false,
        notification_type: '',
        message: ''
    }),
    computed: {
        ...mapState('auth', ['signin_error', 'signin_error_message']),
        ...mapState('data', ['data_error', 'error_message'])
    },
    watch: {
        signin_error(){
            if(this.signin_error){
                this.notification_type = 'error'
                this.message = this.signin_error_message
                this.notificate = true
            }
        },
        data_error(){
            if(this.data_error){
                this.notification_type = 'error';
                this.message = this.error_message;
                this.notificate = true;
            }
        }
    }
}
</script>