<template>
    <v-card>
        <CloseButton v-on:close="$emit('close')"></CloseButton>
        
        <v-card-title>Contatos</v-card-title>

        <v-card-text>
            <v-list two-line>
                <template v-for="(contact, index) in contacts">
                    <v-list-item :key="index">
                        <v-list-item-content>
                            <v-list-item-title v-text="contact.name"></v-list-item-title>
                            <v-list-item-subtitle v-text="contact.description"></v-list-item-subtitle>
                        </v-list-item-content>

                        <v-list-item-action>
                            <v-btn 
                                icon 
                                @click="show_phone=true, selected_contact_name=contact.name, selected_contact_phone=contact.phone.toString()"
                            >
                                <v-icon>fas fa-phone</v-icon>
                            </v-btn>
                            <v-btn icon :href="'mailto:'+contact.email">
                                <v-icon>fas fa-envelope</v-icon>
                            </v-btn>
                        </v-list-item-action>
                    </v-list-item>

                    <v-divider></v-divider>
                </template>
            </v-list>
        </v-card-text>

        <v-dialog v-model="show_phone" max-width="calc(100% - 500px)">
            <v-card>
                <CloseButton v-on:close="show_phone = false"></CloseButton>

                <v-card-title>
                    <v-icon class="mr-5">fas fa-phone</v-icon>
                    Telefone de {{selected_contact_name}}
                </v-card-title>

                <v-card-text>
                    <v-row justify="center" class="ma-0 text-h3 black--text">
                        {{format_phone(selected_contact_phone)}}
                    </v-row>
                </v-card-text>
            </v-card>
        </v-dialog>
    </v-card>
</template>

<script>
import CloseButton from './CloseButton'
import {mapActions, mapState} from 'vuex'
import utils from '../utils/utils'

export default {
    data: () => ({
        show_phone: false,
        selected_contact_name: '',
        selected_contact_phone: ''
    }),
    computed: {
        ...mapState('data', ['contacts'])
    },
    components: {
        CloseButton
    },
    methods: {
        ...mapActions('data', ['get_contacts']),
        format_phone: utils.format_phone
    },
    mounted(){
        this.get_contacts()
    }
}
</script>