<template>
    <v-card height="calc(100vh - 175px)" tile flat class="scroller">
        <v-row v-if="!nothing_to_show" justify="center" class="ma-0">
            <v-col v-for="data in main_data" :key="data.name" cols="4">
                <ParameterCard 
                    :reservoir="reservoir" 
                    :type="data.type" 
                    :value="data.val" 
                    :hide_circle="true" 
                    :hide_search="true" />
            </v-col>
        </v-row>
        
        <v-row v-if="!nothing_to_show" justify="center" class="ma-0">
            <WqiGraph :reservoir="reservoir" :wqi="last_parameters.wqi" />
        </v-row>

        <v-btn
            v-if="!nothing_to_show"
            large
            fab
            bottom
            right
            fixed
            color="primary"
            @click="$emit('detailed_data')"
        >
            <v-icon color="secondary">fas fa-plus</v-icon>
        </v-btn>

        <v-container fluid fill-height v-if="nothing_to_show">
            <v-row justify="center">
              <v-img
                  src='../assets/logo.png'
                  contain
                  max-width="300"
                  max-height="300"
              >
              </v-img>
            </v-row>
            
            <v-row class="text-h3" justify="center"> O reservatório ainda não contém medições </v-row>
          </v-container>
    </v-card>
</template>

<script>
import WqiGraph from '../components/WqiGraph'
import DetailedData from '../components/DetailedData'
import ParameterCard from '../components/ParameterCard'
import {mapActions, mapState} from 'vuex'

export default {
    data: () => ({
        main_data: [],
        view_window: 0,
        nothing_to_show: false
    }),
    computed: {
        ...mapState('data', ['last_parameters'])
    },
    props: {
        reservoir: Number
    },
    components: {
        WqiGraph,
        DetailedData,
        ParameterCard
    },
    watch: {
        reservoir(){
            this.get_last_parameters(this.reservoir)
        },
        last_parameters(){
            if(this.last_parameters != null){
                this.main_data = [
                    {
                        type: 'vol',
                        val: this.last_parameters.volume
                    },
                    {
                        type: 'cons',
                        val: this.last_parameters['daily consum']
                    },
                    {
                        type: 'pred',
                        val: !this.last_parameters['daily consum']?null:this.last_parameters.volume/this.last_parameters['daily consum']
                    }
                ]
            }
            else{
                this.nothing_to_show = true
            }
        }
    },
    methods: {
        ...mapActions('data', ['get_last_parameters'])
    },
    created(){
        if(this.last_parameters == null) this.nothing_to_show = true
    },
    mounted(){
        this.get_last_parameters(this.reservoir)
    }
}
</script>