<template>
    <v-card tile flat>
        <v-toolbar 
            short 
            color="primary" 
            dark 
            class="elevation-0"
        >
            <v-btn icon @click="go_to_home" class="mr-3">
                <v-img width="1" :src="logo" />
            </v-btn>

            <v-spacer></v-spacer>

            <v-menu offset-y>
                <template v-slot:activator="{ on, attrs }">
                    <v-btn icon v-bind="attrs" v-on="on">
                        <v-badge v-if="device_disconnected" dot overlap color="red">
                            <v-icon color="secondary">fa fa-bell</v-icon>
                        </v-badge>
                        <v-icon v-else color="secondary">fa fa-bell</v-icon>
                    </v-btn>
                </template>
                <v-card>
                    <!--<v-row class="ma-0 pa-1" justify="center">
                        <v-btn color="primary" rounded small>
                            Página de alertas
                        </v-btn>
                    </v-row>-->
                    <v-subheader v-if="!device_disconnected">Todos os dispositivos estão conectados</v-subheader>
                    <div v-else>
                        <v-subheader>
                            Dispositivo(s) desconectado(s)
                        </v-subheader>
                        <v-list-item
                            v-for="(disconnected_reservoir, i) in disconnected_reservoirs"
                            :key="i"
                        >
                            <v-list-item-avatar>
                                <v-icon color="red">fas fa-exclamation</v-icon>
                            </v-list-item-avatar>
                            <v-list-item-content>
                                <v-list-item-title>{{disconnected_reservoir.name}}</v-list-item-title>
                            </v-list-item-content>
                        </v-list-item>
                    </div>
                </v-card>
                
            </v-menu>

            <v-btn @click="signout" icon>
                <v-icon color="secondary">fas fa-sign-out-alt</v-icon>
            </v-btn>
        </v-toolbar>
    </v-card>
</template>

<script>
import { mapState, mapActions } from "vuex";

export default {
    data: () => ({
        logo: require('../assets/logo.png')
    }),
    computed: {
        ...mapState("data", ["device_disconnected", "disconnected_reservoirs"])
    },  
    methods: {
        ...mapActions("data", ["check_connection"]),
        go_to_home(){
            if (this.$router.currentRoute.name != "Home") {
                this.$router.push({ name: "Home" })
            }
        },
        signout(){
            this.$router.push({ path: "/signout" })
        }
    },
    mounted(){
        this.check_connection();
    }
}
</script>