export default{
get_wqi_icon(wqi){
    if (wqi <= 25) {
        return require('../assets/emoji_0.png'); //excelente
    } else if (wqi <= 50) {
        return require('../assets/emoji_1.png'); //boa
    } else if (wqi <= 75) {
        return require('../assets/emoji_2.png'); //ruim
    } else if (wqi <= 100) {
        return require('../assets/emoji_3.png'); //péssima
    } else {
        return require('../assets/emoji_4.png'); //inadequada para uso
    }
},
get_wqi_title(wqi) {
    if (wqi <= 25) {
        return 'Qualidade Excelente!'; //excelente
    } else if (wqi <= 50) {
        return 'Qualidade Boa!'; //boa
    } else if (wqi <= 75) {
        return 'Qualidade Ruim!'; //ruim
    } else if (wqi <= 100) {
        return 'Qualidade Péssima!'; //péssima
    } else {
        return 'Inadequada para uso!'; //inadequada para uso
    }
},
parameters_tolerance(type, value){
    if(type == 'wqi'){
        if (value < 45) return 'green'
        else if (value <= 50) return 'yellow'
        else return 'red'
    }
    else if(type == 'temp'){
        if (value >= 0 && value <= 27) return 'green'
        else if (value <= 30) return 'yellow'
        else return 'red'
    }
    else if(type == 'ph'){
        if (value >= 6.5 && value <= 8.5) return 'green'
        else if ((value >= 6 && value < 6.5) || (value > 8.5 && value < 9.0)) return 'yellow'
        else return 'red'
    }
    else if(type == 'do'){
        if (value > 5.5) return 'green'
        else if (value >= 5) return 'yellow'
        else return 'red'
    }
    else if(type == 'ec'){
        if (value < 95) return 'green'
        else if (value <= 100) return 'yellow'
        else return 'red'
    }
    else if(type == 'tds'){
        if (value < 450) return 'green'
        else if (value <= 500) return 'yellow'
        else return 'red'
    }
    else if(type == 'sal'){
        if (value < 0.6) return 'green'
        else if (value <= 1) return 'yellow'
        else return 'red'
    }
    else if(type == 'turb'){
        if (value < 4) return 'green'
        else if (value <= 5) return 'yellow'
        else return 'red'
    }
    else if(type == 'vol'){
        if (value > 200) return 'green'
        else if (value > 100) return 'yellow'
        else return 'red'
    }
},
format_phone(phone) {
    var telefoneLength = phone.replace(/\D/g, "").length;
    var firstNumber = phone.replace(/\D/g, "")[0];
    var forFirstNumbers = phone.replace(/\D/g, "").slice(0, 4);
    var x;

    if (forFirstNumbers == "0800") {
        x = phone
        .replace(/\D/g, "")
        .match(/(\d{0,4})(\d{0,3})(\d{0,3})/);
        phone = !x[2] ? x[1] : x[1] + " " + x[2] + " " + x[3];
    } else if (telefoneLength <= 10) {
        x = phone
        .replace(/\D/g, "")
        .match(/(\d{0,2})(\d{0,4})(\d{0,4})/);
        phone = !x[2]
        ? x[1]
        : "(" + x[1] + ") " + x[2] + (x[3] ? "-" + x[3] : "");
    } else if (telefoneLength <= 11 && firstNumber == "0") {
        x = phone
        .replace(/\D/g, "")
        .match(/(\d{0,3})(\d{0,4})(\d{0,4})/);
        phone = !x[2]
        ? x[1]
        : "(" + x[1] + ") " + x[2] + (x[3] ? "-" + x[3] : "");
    } else if (telefoneLength <= 11) {
        x = phone
        .replace(/\D/g, "")
        .match(/(\d{0,2})(\d{0,1})(\d{0,4})(\d{0,4})/);
        phone = !x[2]
        ? x[1]
        : "(" + x[1] + ") " + x[2] + " " + x[3] + (x[4] ? "-" + x[4] : "");
    } else if (telefoneLength <= 12 && firstNumber == "0") {
        x = phone
        .replace(/\D/g, "")
        .match(/(\d{0,3})(\d{0,1})(\d{0,4})(\d{0,4})/);
        phone = !x[2]
        ? x[1]
        : "(" + x[1] + ") " + x[2] + " " + x[3] + (x[4] ? "-" + x[4] : "");
    }

    return phone
},
allow_dates_until_today(date){
    date = new Date(date)

    if(date > Date.now()) return false

    return true
},
format_dates(dates){
    let formatted_dates = ""

    var [year, month, day] = dates[0].split('-')
    formatted_dates = day+"/"+month+"/"+year

    if(dates.length > 1){
        [year, month, day] = dates[1].split('-')
        formatted_dates += " - "+day+"/"+month+"/"+year
    }

    return formatted_dates
},
min_date(dates){
    let min = dates[0]

    dates.forEach(date => {
        if(date < min) min = date
    })

    return min
},
max_date(dates){
    let max = dates[0]

    dates.forEach(date => {
        if(date > max) max = date
    })

    return max
}
}