import firebase from "firebase";

import {
    SIGNIN_BEGIN,
    SET_USER,
    SET_EMAIL,
    SIGNIN_SUCCESS,
    SIGNIN_FAILURE
} from './types'

const USER_STORAGE_KEY = "G7TPUZEy86N77C6t33vp"
const EMAIL_STORAGE_KEY = "G7TPUZEy86N77C6t12g"

const initialState = {
    authenticating: false,
    signin_success: false,
    signin_error: false,
    signin_error_message: '',
    user: null,
    email: ""
}

const getters = {
    isAuthenticated: state => !!state.user
}

const actions = {
    signin({ commit }, { username, password }) {
        commit(SIGNIN_BEGIN)
        
        return firebase.auth().signInWithEmailAndPassword(username, password)
        .then(() => {
            commit(SIGNIN_SUCCESS)
        })
        .catch(err => {
            commit(SIGNIN_FAILURE, err.message)
        });
    },
    signout({ commit }) {
        return firebase.auth().signOut()
        .then(() => {
            commit(SET_USER, null);
            commit(SET_EMAIL, null);
        })
    },
    fetch_user({ commit }, user) {
        if (user) {
            commit(SET_USER, user);
        } else {
            commit(SET_USER, null);
        }
    },
    fetch_email({ commit }, email) {
        if (email) {
            commit(SET_EMAIL, email);
        } else {
            commit(SET_EMAIL, null);
        }
    },
    initialize({ commit }) {
        const user_data = localStorage.getItem(USER_STORAGE_KEY);
        const user_email = localStorage.getItem(EMAIL_STORAGE_KEY);
        
        if (user_data && user_email) {
            commit(SET_USER, user_data);
            commit(SET_EMAIL, user_email);
        }
        else{
            commit(SET_USER, null);
            commit(SET_EMAIL, null);
        }
    },
}

const mutations = {
    [SIGNIN_BEGIN](state){
        state.authenticating = true;
        state.signin_error = false;
        state.signin_success = false;
    },
    [SET_USER](state, data){
        if(data == null){
            state.user = null
            localStorage.removeItem(USER_STORAGE_KEY)
        }
        else{
            state.user = data;
            localStorage.setItem(USER_STORAGE_KEY, data)
        }
    },
    [SET_EMAIL](state, email){
        if(email == null){
            state.email = null
            localStorage.removeItem(EMAIL_STORAGE_KEY)
        }
        else{
            state.email = email;
            localStorage.setItem(EMAIL_STORAGE_KEY, email)
        }
    },
    [SIGNIN_SUCCESS](state) {
        state.authenticating = false;
        state.signin_error = false;
        state.signin_success = true;
    },
    [SIGNIN_FAILURE](state, error) {
        state.authenticating = false;
        state.signin_error = true;

        state.signin_error_message = error
    }
}

export default {
    namespaced: true,
    state: initialState,
    getters,
    actions,
    mutations
  }