<template>
  <v-container fluid>
    <v-row>
      <v-col cols="2">
        <FilterColumn 
          v-on:go_to_reservoir="update_reservoir" 
          v-on:report="open_report_dialog = true" 
          v-on:contacts="open_contacts_dialog = true"
        />
      </v-col>

      <v-col>
        <v-slide-y-transition>
          <v-container fluid fill-height v-if="reservoir == undefined">
            <v-row justify="center">
              <v-img
                  src='../assets/logo.png'
                  contain
                  max-width="300"
                  max-height="300"
              >
              </v-img>
            </v-row>
            
            <v-row class="text-h3" justify="center"> Selecione um reservatório </v-row>
          </v-container>

          <v-window v-model="view_window" class="pb-5" v-else>
            <v-row class="ma-0">
              <v-card-title>{{reservoir.name}}</v-card-title>
              <v-spacer></v-spacer>
              <v-alert type="error" dense outlined v-if="disconnected_reservoirs.find(el => el.id == reservoir.id)">
                Reservatório Desconectado
              </v-alert>
            </v-row>

            <v-window-item>
              <MainData v-if="view_window == 0" :reservoir="reservoir.id" v-on:detailed_data="view_window = 1" />
            </v-window-item>

            <v-window-item>
              <DetailedData v-if="view_window == 1" :reservoir="reservoir.id" v-on:back="view_window = 0" />
            </v-window-item>
          </v-window>
        </v-slide-y-transition>
      </v-col>
    </v-row>

    <v-dialog v-model="open_report_dialog">
      <Report v-on:close="open_report_dialog=false"/>
    </v-dialog>

    <v-dialog v-model="open_contacts_dialog">
      <Contacts v-on:close="open_contacts_dialog=false"/>
    </v-dialog>
  </v-container>
</template>

<script>
import FilterColumn from "../components/FilterColumn"
import MainData from "../components/MainData"
import Report from "../components/Report"
import DetailedData from '../components/DetailedData'
import Contacts from '../components/Contacts'
import { mapState } from "vuex";

export default {
  name: 'Home',
  data: () => ({
    reservoir: undefined,
    open_report_dialog: false,
    view_window: 0,
    open_contacts_dialog: false
  }),
  computed: {
    ...mapState("data", ["disconnected_reservoirs"])
  },
  components: {
    FilterColumn,
    MainData,
    Report,
    DetailedData,
    Contacts
  },
  methods: {
    update_reservoir(reservoir){
      this.reservoir = reservoir
      this.view_window = 0
    }
  }
}
</script>
