<template>
    <v-card>
        <CloseButton v-on:close="$emit('close')"></CloseButton>
        
        <v-card-title>Relatório</v-card-title>

        <v-card-subtitle>
            <v-row justify="center" class="ma-0" align="center">
                <b>Reservatórios:</b>
                <v-item-group
                    v-model="view_window"
                    class="text-center ml-2"
                    mandatory
                >
                    <v-item
                        v-for="(reservoir, i) in reservoirs"
                        :key="i"
                        v-slot="{ active, toggle }"
                    >
                    <v-btn
                        :input-value="active"
                        @click="toggle"
                        text
                        rounded
                        color="primary"
                    >
                        {{reservoir.name}}
                    </v-btn>
                    </v-item>
                </v-item-group>
            </v-row>
        </v-card-subtitle>  

        <v-card-text>
            <v-menu
                ref="menu"
                v-model="menu"
                :close-on-content-click="false"
                :return-value.sync="dates"
                transition="scale-transition"
                offset-y
                min-width="auto"
            >
                <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                        v-model="formatted_dates"
                        label="Selecione o intervalo"
                        prepend-icon="mdi-calendar"
                        readonly
                        v-bind="attrs"
                        v-on="on"
                    ></v-text-field>
                </template>
                <v-date-picker
                    v-model="dates"
                    range
                    :allowed-dates="allow_dates_until_today"
                    locale="PT"
                    :selected-items-text="dates.length.toString() + ' selecionado' + (dates.length>1?'s':'')"
                    @input="dates.length==2?$refs.menu.save(dates):''"
                ></v-date-picker>
            </v-menu>

            <v-window touchless v-model="view_window">
                <v-window-item
                    v-for="(reservoir, i) in reservoirs"
                    :key="i"
                >
                    <v-card tile flat>
                        <v-card-title>
                            Relatório do(a) {{reservoir.name}}
                        </v-card-title>

                        <v-data-table
                            :headers="headers"
                            :items="data"
                        >
                        </v-data-table>
                    </v-card>
                </v-window-item>
            </v-window>
        </v-card-text>

        <v-card-actions>
            <v-btn color="primary secondary--text" rounded @click="generate_all_reports">
                Gerar todos os Relatórios
            </v-btn>
            <v-spacer></v-spacer>
            
            <v-btn color="primary secondary--text" rounded @click="generate_report(reservoirs[view_window].id)">
                Gerar Relatório de {{reservoirs[view_window].name}}
            </v-btn>
        </v-card-actions>
    </v-card>
</template>

<script>
import CloseButton from './CloseButton'
import {mapActions, mapState} from 'vuex'
import utils from '../utils/utils'

export default {
    data: () => ({
        view_window: 0,
        dates: [],
        formatted_dates: "",
        menu: false,
        headers: [
            {text: 'Data', value: 'date', sortable: false},
            {text: 'pH', value: 'ph', sortable: false},
            {text: 'Oxigênio Dissolvido', value: 'do', sortable: false},
            {text: 'Condutividade Elétrica', value: 'ec', sortable: false},
            {text: 'Salinidade', value: 'sal', sortable: false},
            {text: 'T.D.S.', value: 'tds', sortable: false},
            {text: 'Turbidez', value: 'turb', sortable: false},
            {text: 'Temperatura', value: 'temp', sortable: false},
            {text: 'Volume', value: 'vol', sortable: false},
            {text: 'I.Q.A.', value: 'wqi', sortable: false},
            {text: 'Consumo', value: 'cons', sortable: false}
        ],
        data: []
    }),
    computed: {
        ...mapState('data', ['reservoirs', 'report_data'])
    },
    components: {
        CloseButton
    },
    watch: {
        report_data(){
            this.update_data()
        },
        view_window(){
            this.update_data()
        },
        dates(){
            this.formatted_dates = this.format_dates(this.dates)

            this.get_report_measurements(this.dates)
        }
    },
    methods: {
        ...mapActions('data', ['get_reservoirs', 'get_report_measurements', "get_report"]),
        allow_dates_until_today: utils.allow_dates_until_today,
        format_dates: utils.format_dates,
        min_date: utils.min_date,
        max_date: utils.max_date,
        update_data(){
            let report_data = this.report_data.find(element => element.reservoir == this.reservoirs[this.view_window].id)
            
            if(report_data != undefined){
                let data = JSON.parse(JSON.stringify(report_data.data))

                this.data = data.map(function(data) {
                    Object.keys(data).forEach(element => {
                        data[element] = JSON.stringify(data[element]).replaceAll('{', '').replaceAll('}', '').replaceAll(',', '\n').replaceAll('"', '')
                    });

                    return data
                })
            }
            else{
                this.data = []
            }
        },
        generate_report(reservoir_id){
            this.get_report({"reservoir_id": reservoir_id, "time1": utils.min_date(this.dates), "time2": utils.max_date(this.dates)})
        },
        generate_all_reports(){
            this.get_report({"reservoir_id": "all", "time1": utils.min_date(this.dates), "time2": utils.max_date(this.dates)})
        },
        init_report_interval(){
            let today = new Date(Date.now())
            let first_month_day = new Date(today.getFullYear(), today.getMonth(), 1);

            this.dates.push(first_month_day.toISOString().split('T')[0])
            this.dates.push(today.toISOString().split('T')[0])
        }
    },
    mounted(){
        this.get_reservoirs()

        this.init_report_interval()

        this.get_report_measurements(this.dates)
    }
}
</script>