import firebase from "firebase";
import utils from "../utils/utils"
import session from '../api/session'
import download from 'downloadjs'

import {
    LOADING_DATA,
    CLEAR_RESERVOIRS,
    PUSH_RESERVOIR,
    DATA_LOADED,
    GET_LAST_PARAMETERS,
    CLEAR_CONTACTS,
    PUSH_CONTACT,
    PUSH_MEASUREMENTS,
    CLEAR_MEASUREMENTS,
    REPORT_DATA,
    CLEAR_REPORT_DATA,
    DATA_ERROR,
    DEVICE_DISCONNECTED
} from './types'

const initialState = {
    loading_data: false,
    reservoirs: [],
    last_parameters: {},
    contacts: [],
    value_measurements: [],
    report_data: [],
    data_error: false,
    error_message: "",
    device_disconnected: false,
    disconnected_reservoirs: []
}

const actions = {
    async get_reservoirs({ commit, rootState}){
        commit(LOADING_DATA)

        //return firebase.firestore().collection('users/XUJsvsn1s5XNR2twP0rklGV3Pgu1/reservoirs').get()
        return firebase.firestore().collection('users/'+rootState.auth.user+'/reservoirs').get()
        .then((reservoirs) => {
            commit(CLEAR_RESERVOIRS)
            
            reservoirs.forEach(reservoir => {
                commit(PUSH_RESERVOIR, reservoir.data())
            })

            commit(DATA_LOADED)
        })
        .catch(() => {
            commit(DATA_LOADED);

            commit(DATA_ERROR, "Erro ao carregar reservatórios");
        })
    },
    check_connection({ commit, rootState }){
        commit(LOADING_DATA);

        commit(DEVICE_DISCONNECTED, {"status": false, "name": ""});

        firebase.firestore().collection('users/'+rootState.auth.user+'/reservoirs').get()
        .then((reservoirs) => {
            reservoirs.forEach(reservoir => {
                const id = reservoir.data().id<10?'0'+reservoir.data().id.toString():reservoir.data().id.toString()
                
                firebase.firestore().collection('users/'+rootState.auth.user+'/reservoirs/'+id+"/measurements").orderBy("time", "desc").limit(1).get()
                .then((measurement) => {
                    if(measurement.docs.length > 0){
                        if ((new Date() - new Date(measurement.docs[0].data().time.toDate())) / 3600000 > 6.5){
                            commit(DEVICE_DISCONNECTED, {"status": true, "reservoir": reservoir.data()});
                        }
                    }
                })
                .catch(() => {
                    commit(DATA_LOADED);
        
                    commit(DATA_ERROR, "Erro ao carregar últimos parâmetros de medição");
                })
            })

            commit(DATA_LOADED)
        })
        .catch(() => {
            commit(DATA_LOADED);

            commit(DATA_ERROR, "Erro ao carregar reservatórios");
        })
    },
    get_last_parameters({ commit, rootState }, reservoir_id){
        commit(LOADING_DATA)

        let id = reservoir_id<10?'0'+reservoir_id.toString():reservoir_id.toString()

        //return firebase.firestore().collection('users/XUJsvsn1s5XNR2twP0rklGV3Pgu1/reservoirs/'+id+"/measurements").orderBy("time", "desc").limit(1).get()
        return firebase.firestore().collection('users/'+rootState.auth.user+'/reservoirs/'+id+"/measurements").orderBy("time", "desc").limit(1).get()
        .then((measurement) => {
            if(measurement.docs[0] != undefined) commit(GET_LAST_PARAMETERS, measurement.docs[0].data())
            else commit(GET_LAST_PARAMETERS, null)

            commit(DATA_LOADED)
        })
        .catch(() => {
            commit(DATA_LOADED);

            commit(DATA_ERROR, "Erro ao carregar últimos parâmetros de medição");
        })
    },
    get_all_measurements({ commit, rootState }, {reservoir_id, type}){
        commit(LOADING_DATA)

        let id = reservoir_id<10?'0'+reservoir_id.toString():reservoir_id.toString()

        //return firebase.firestore().collection('users/XUJsvsn1s5XNR2twP0rklGV3Pgu1/reservoirs/'+id+"/measurements").orderBy("time").get()
        return firebase.firestore().collection('users/'+rootState.auth.user+'/reservoirs/'+id+"/measurements").get()
        .then((measurements) => {
            commit(CLEAR_MEASUREMENTS)

            measurements.docs.forEach(measurement => {
                let time = measurement.data().time.toDate().toLocaleString()
                let value = null
                
                if(type == 'wqi')          value = measurement.data().wqi
                else if(type == 'temp')    value = measurement.data().temp
                else if(type == 'ph')      value = measurement.data().pH
                else if(type == 'do')      value = measurement.data().OD
                else if(type == 'ec')      value = measurement.data().EC
                else if(type == 'tds')     value = measurement.data().TDS
                else if(type == 'sal')     value = measurement.data().salinity
                else if(type == 'turb')    value = measurement.data().turb
                else if(type == 'vol')     value = measurement.data().volum
                else if(type == 'cons')    value = measurement.data()['daily consum']
                
                commit(PUSH_MEASUREMENTS, {'time': time, 'value': value})
            })

            commit(DATA_LOADED)
        })
        .catch(() => {
            commit(DATA_LOADED);

            commit(DATA_ERROR, "Erro ao carregar dados de medição");
        })
    },
    async get_report_measurements({ commit, rootState}, dates){
        commit(LOADING_DATA)

        let reservoirs = await firebase.firestore().collection('users/'+rootState.auth.user+'/reservoirs/').get()

        commit(CLEAR_REPORT_DATA)
        //console.log(dates)

        reservoirs.docs.forEach(reservoir => {
            firebase.firestore().collection('users/'+rootState.auth.user+'/reservoirs/'+reservoir.id+"/measurements").get()
            .then((measurements) => {
                let report_data = []

                measurements.docs.forEach(measurement => {
                    let date = measurement.data().time.toDate().toLocaleString().split(' ')[0]
                    
                    let report_measurement = report_data.find(element => element.date == date)
                    
                    let inside_interval = true
                    let measure_date = measurement.data().time.toDate()
                    dates = dates.map(function(date){return new Date(date)})
                    let date1 = utils.min_date(dates)
                    let date2 = utils.max_date(dates)
                    if(measure_date < date1 || measure_date > date2) inside_interval = false
                    
                    if(inside_interval){
                        if(report_measurement == undefined){
                            report_data.push({
                                'date': date,
                                'ph':{
                                    'min': measurement.data().pH.toFixed(2),
                                    'sum': measurement.data().pH,
                                    'max': measurement.data().pH.toFixed(2)
                                },
                                'do':{
                                    'min': measurement.data().OD.toFixed(2),
                                    'sum': measurement.data().OD,
                                    'max': measurement.data().OD.toFixed(2)
                                },
                                'ec':{
                                    'min': measurement.data().EC.toFixed(2),
                                    'sum': measurement.data().EC,
                                    'max': measurement.data().EC.toFixed(2)
                                },
                                'sal':{
                                    'min': measurement.data().salinity.toFixed(2),
                                    'sum': measurement.data().salinity,
                                    'max': measurement.data().salinity.toFixed(2)
                                },
                                'tds':{
                                    'min': measurement.data().TDS.toFixed(2),
                                    'sum': measurement.data().TDS,
                                    'max': measurement.data().TDS.toFixed(2)
                                },
                                'turb':{
                                    'min': measurement.data().turb.toFixed(2),
                                    'sum': measurement.data().turb,
                                    'max': measurement.data().turb.toFixed(2)
                                },
                                'temp':{
                                    'min': measurement.data().temp.toFixed(2),
                                    'sum': measurement.data().temp,
                                    'max': measurement.data().temp.toFixed(2)
                                },
                                'vol':{
                                    'min': measurement.data().volum!=undefined?measurement.data().volum.toFixed(2):0,
                                    'sum': measurement.data().volum,
                                    'max': measurement.data().volum!=undefined?measurement.data().volum.toFixed(2):0
                                },
                                'wqi':{
                                    'min': measurement.data().wqi.toFixed(2),
                                    'sum': measurement.data().wqi,
                                    'max': measurement.data().wqi.toFixed(2)
                                },
                                'cons':{
                                    'min': measurement.data()['daily consum'].toFixed(2),
                                    'sum': measurement.data()['daily consum'],
                                    'max': measurement.data()['daily consum'].toFixed(2)
                                },
                                'qtd': 1
                            })
                        }
                        else{
                            report_measurement.ph.min = Math.min(report_measurement.ph.min, measurement.data().pH).toFixed(2)
                            report_measurement.ph.sum += measurement.data().pH
                            report_measurement.ph.max = Math.max(report_measurement.ph.max, measurement.data().pH).toFixed(2)

                            report_measurement.do.min = Math.min(report_measurement.do.min, measurement.data().OD).toFixed(2)
                            report_measurement.do.sum += measurement.data().OD
                            report_measurement.do.max = Math.max(report_measurement.do.max, measurement.data().OD).toFixed(2)

                            report_measurement.ec.min = Math.min(report_measurement.ec.min, measurement.data().EC).toFixed(2)
                            report_measurement.ec.sum += measurement.data().EC
                            report_measurement.ec.max = Math.max(report_measurement.ec.max, measurement.data().EC).toFixed(2)

                            report_measurement.sal.min = Math.min(report_measurement.sal.min, measurement.data().salinity).toFixed(2)
                            report_measurement.sal.sum += measurement.data().salinity
                            report_measurement.sal.max = Math.max(report_measurement.sal.max, measurement.data().salinity).toFixed(2)

                            report_measurement.tds.min = Math.min(report_measurement.tds.min, measurement.data().TDS).toFixed(2)
                            report_measurement.tds.sum += measurement.data().TDS
                            report_measurement.tds.max = Math.max(report_measurement.tds.max, measurement.data().TDS).toFixed(2)

                            report_measurement.turb.min = Math.min(report_measurement.turb.min, measurement.data().turb).toFixed(2)
                            report_measurement.turb.sum += measurement.data().turb
                            report_measurement.turb.max = Math.max(report_measurement.turb.max, measurement.data().turb).toFixed(2)

                            report_measurement.temp.min = Math.min(report_measurement.temp.min, measurement.data().temp).toFixed(2)
                            report_measurement.temp.sum += measurement.data().temp
                            report_measurement.temp.max = Math.max(report_measurement.temp.max, measurement.data().temp).toFixed(2)

                            report_measurement.vol.min = Math.min(report_measurement.vol.min, measurement.data().volum).toFixed(2)
                            report_measurement.vol.sum += measurement.data().volum
                            report_measurement.vol.max = Math.max(report_measurement.vol.max, measurement.data().volum).toFixed(2)

                            report_measurement.wqi.min = Math.min(report_measurement.wqi.min, measurement.data().wqi).toFixed(2)
                            report_measurement.wqi.sum += measurement.data().wqi
                            report_measurement.wqi.max = Math.max(report_measurement.wqi.max, measurement.data().wqi).toFixed(2)

                            report_measurement.cons.min = Math.min(report_measurement.cons.min, measurement.data()['daily consum']).toFixed(2)
                            report_measurement.cons.sum += measurement.data()['daily consum']
                            report_measurement.cons.max = Math.max(report_measurement.do.max, measurement.data()['daily consum']).toFixed(2)

                            report_measurement.qtd++
                        }
                    }
                })

                report_data.forEach(report_day => {
                    report_day.ph['med'] = (report_day.ph.sum / report_day.qtd).toFixed(2)
                    delete report_day.ph.sum

                    report_day.do['med'] = (report_day.do.sum / report_day.qtd).toFixed(2)
                    delete report_day.do.sum

                    report_day.ec['med'] = (report_day.ec.sum / report_day.qtd).toFixed(2)
                    delete report_day.ec.sum

                    report_day.sal['med'] = (report_day.sal.sum / report_day.qtd).toFixed(2)
                    delete report_day.sal.sum

                    report_day.tds['med'] = (report_day.tds.sum / report_day.qtd).toFixed(2)
                    delete report_day.tds.sum

                    report_day.turb['med'] = (report_day.turb.sum / report_day.qtd).toFixed(2)
                    delete report_day.turb.sum

                    report_day.temp['med'] = (report_day.temp.sum / report_day.qtd).toFixed(2)
                    delete report_day.temp.sum

                    report_day.vol['med'] = (report_day.vol.sum / report_day.qtd).toFixed(2)
                    delete report_day.vol.sum

                    report_day.wqi['med'] = (report_day.wqi.sum / report_day.qtd).toFixed(2)
                    delete report_day.wqi.sum

                    report_day.cons['med'] = (report_day.cons.sum / report_day.qtd).toFixed(2)
                    delete report_day.cons.sum

                    delete report_day.qtd
                })
                
                commit(REPORT_DATA, {'report_data': report_data, 'reservoir': reservoir.id});
                commit(DATA_LOADED);
            })
            .catch(() => {
                commit(DATA_LOADED);
    
                commit(DATA_ERROR, "Erro ao carregar dados de relatório");
            })
        })
    },
    get_report({commit, rootState}, {reservoir_id, time1, time2}){
        commit(LOADING_DATA)

        let email = rootState.auth.email
        
        return session.get('webReport/?email='+email+'&reservoirId='+reservoir_id+'&time1='+time1+'%2000:00:00.0&time2='+time2+'%2000:00:00.0')
        //return session.get('webReport/?email=rcrs4@cin.ufpe.br&reservoirId='+reservoir_id+'&time1='+time1+'%2000:00:00.0&time2='+time2+'%2000:00:00.0')
        .then( response => {
            const content = response.headers['content-type'];
            download(response.data, 'relatório.pdf', content);

            commit(DATA_LOADED);
        })
        .catch(function () {
            commit(DATA_LOADED);

            commit(DATA_ERROR, "Erro ao baixar o relatório");
        });
    },
    get_contacts({commit}){
        commit(LOADING_DATA)

        return firebase.firestore().collection('contacts').get()
        .then((contacts) => {
            commit(CLEAR_CONTACTS)

            contacts.forEach(contact => {
                commit(PUSH_CONTACT, contact.data())
            })

            commit(DATA_LOADED)
        })
        .catch(function () {
            commit(DATA_LOADED);

            commit(DATA_ERROR, "Erro ao carregar contatos");
        });
    }
}

const mutations = {
    [LOADING_DATA](state){
        state.loading_data = true;
        state.data_error = false;
    },
    [CLEAR_RESERVOIRS](state){
        state.reservoirs = []
    },
    [PUSH_RESERVOIR](state, reseroir){
        state.reservoirs.push(reseroir)
    },
    [DATA_LOADED](state){
        state.loading_data = false
    },
    [DATA_ERROR](state, message){
        state.error_message = message;

        state.data_error = true;
    },
    [DEVICE_DISCONNECTED](state, {status, reservoir}){
        state.device_disconnected = status;
        if(status == false){
            state.disconnected_reservoirs = [];
        }
        else{
            state.disconnected_reservoirs.push(reservoir);
        }
    },
    [GET_LAST_PARAMETERS](state, parameters){
        state.last_parameters = parameters
    },
    [CLEAR_CONTACTS](state){
        state.contacts = []
    },
    [PUSH_CONTACT](state, contact){
        state.contacts.push(contact)
    },
    [CLEAR_MEASUREMENTS](state){
        state.value_measurements = []
    },
    [PUSH_MEASUREMENTS](state, value_measurements){
        state.value_measurements.push(JSON.parse(JSON.stringify(value_measurements)));
    },
    [CLEAR_REPORT_DATA](state){
        state.report_data = []
    },
    [REPORT_DATA](state, {report_data, reservoir}){
        state.report_data.push({
            'reservoir': reservoir,
            'data': report_data
        })
    }
}

export default {
    namespaced: true,
    state: initialState,
    actions,
    mutations
  }