<template>
    <v-card 
        class="rounded-xl"
        flat
        tile
        width="calc(100% - 100px)"
    >   
        <v-card-title>
            <v-card flat tile>
                <v-img :src="get_wqi_icon(wqi)" height="100" width="100" contain></v-img>
            </v-card>

            <p class="ml-10 text-h4">{{get_wqi_title(wqi)}}</p>
        </v-card-title>

        <v-card-text>
            O Índice de Qualidade das Águas (IQA) é o principal indicador qualitativo usado no país. Foi desenvolvido para avaliar a qualidade da água para o abastecimento público, após o tratamento convencional.
        </v-card-text>
        
        <Graph class="ma-5" type="wqi" :reservoir="reservoir" />
    </v-card>
</template>

<script>
import Utils from '../utils/utils'
import {mapState} from 'vuex'
import Graph from './Graph'

export default {
    props: {
        reservoir: Number,
        wqi: Number
    },
    computed: {
        ...mapState('data', ['last_parameters'])
    },
    components: {
        Graph
    },
    methods: {
        get_wqi_icon: Utils.get_wqi_icon,
        get_wqi_title: Utils.get_wqi_title
    }
}
</script>