<template>
    <v-card class="px-10">
        <CloseButton v-on:close="$emit('close')"/>

        <v-card-title>{{title}}</v-card-title>

        <v-subheader v-if="info.length > 0">Info</v-subheader>
        <v-card-text v-if="info.length > 0">{{info}}</v-card-text>

        <v-subheader>Histórico</v-subheader>

        <Graph :type="type" :reservoir="reservoir" />

        <v-btn
            absolute
            right
            :style="{bottom: '50%', transform:'translateY(-50%)'}"
            icon
            :disabled="type == 'vol'"
            @click="$emit('next', type)"
        >
                <v-icon>fas fa-arrow-right</v-icon>
        </v-btn>

        <v-btn
            absolute
            left
            :style="{bottom: '50%', transform:'translateY(-50%)'}"
            icon
            :disabled="type == 'wqi'"
            @click="$emit('back', type)"
        >
                <v-icon>fas fa-arrow-left</v-icon>
        </v-btn>
    </v-card>
</template>

<script>
import CloseButton from './CloseButton'
import Graph from './Graph'

export default {
    data: () => ({
        title: '',
        info: ''
    }),
    props: {
        type: String,
        reservoir: Number
    },
    components: {
        CloseButton,
        Graph
    },
    watch: {
        type(){
            this.update_data()
        }
    },
    methods: {
        update_data(){
            if(this.type == 'wqi'){
                this.title = 'IQA'
                this.info = 'O Índice de Qualidade das Águas (IQA) é o principal indicador qualitativo usado no país. Foi desenvolvido para avaliar a qualidade da água para o abastecimento público, após o tratamento convencional.'
            }
            else if(this.type == 'temp'){
                this.title = 'Temperatura'
                this.info = 'Variações de temperatura são parte do regime climático normal dependendo da altitude, latitude, estação do ano, período do dia, taxa de fluxo e profundidade. Em geral, à medida que a temperatura aumenta, de 0 a 30°C, alguns fatores diminuem como a viscosidade, tensão superficial, compressibilidade, calor específico, constante de ionização e calor latente de vaporização, enquanto a condutividade elétrica e térmica, a pressão de vapor, a solubilidade de sais aumentam com a elevação da temperatura.'
            }
            else if(this.type == 'ph'){
                this.title = 'pH'
                this.info = 'É usado para indicar o quão ácida ou básica está a água, ou seja, expressa a concentração de íons de hidrogênio no líquido. Alterações nos valores de pH também podem aumentar o efeito de substâncias químicas que são tóxicas para os organismos aquáticos, tais como os metais pesados.'
            }
            else if(this.type == 'do'){
                this.title = 'Oxigênio Dissolvido'
                this.info = 'As águas poluídas por esgotos apresentam baixa concentração de oxigênio dissolvido pois o mesmo é consumido no processo de decomposição da matéria orgânica. Por outro lado as águas limpas apresentam concentrações de oxigênio dissolvido mais elevadas.'
            }
            else if(this.type == 'ec'){
                this.title = 'Condutividade Elétrica'
                this.info = 'É a capacidade que a água possui de conduzir corrente elétrica. É uma medida da concentração total de sais dissolvidos presentes na água. Apesar de não fornecer medidas reais da concentração de um determinado íon presente, ela nos dá uma noção muito boa do quanto a água está impactada, o que indiretamente sugere a origem e o grau de contaminação da água.'
            }
            else if(this.type == 'tds'){
                this.title = 'Totais de Sólidos Dissolvidos.'
                this.info = 'Os totais sólidos dissolvidos (TSD) compreendem sais inorgânicos (principalmente cálcio, magnésio, potássio, sódio, bicarbonatos, cloretos e sulfatos) e algumas pequenas quantidades de matéria orgânica que são dissolvidas em água.'
            }
            else if(this.type == 'sal'){
                this.title = 'Salinidade'
                this.info = 'Salinidade é uma medida da quantidade de sais existentes em massas de água naturais. A forma mais simples de descrever a salinidade é como a relação entre o conteúdo de sais dissolvidos em uma dada quantidade de água.'
            }
            else if(this.type == 'turb'){
                this.title = 'Turbidez'
                this.info = 'A Turbidez da água é causada pela presença de partículas em suspensão ou materiais como argilas, terra, areia, plâncton, óxidos, hidróxidos e carbonatos minerais.'
            }
            else if(this.type == 'vol'){
                this.title = 'Volume'
                this.info = ''
            }
            else if(this.type == 'cons'){
                this.title = 'Consumo'
                this.info = ''
            }
            else if(this.type == 'pred'){
                this.title = 'Predição'
                this.info = ''
            }
        }
    },
    mounted(){
        this.update_data()
    }
}
</script>