<template>
    <v-card height="calc(100vh - 90px)" tile flat>
        <v-row class="ma-0" justify="center">
            <v-btn 
                rounded 
                color="primary secondary--text" 
                large 
                @click="$emit('report')"
            >
                <v-icon class="mr-2">fas fa-chart-line</v-icon>
                Gerar Relatório
            </v-btn>
        </v-row>

        <v-divider class="mt-5"></v-divider>

        <v-row class="ma-0 text-h6">Reservatórios</v-row>

        <v-text-field prepend-icon="fas fa-search" label="Pesquisar" v-model="search_resevoir_string" @input="search_reservoir(search_resevoir_string)"></v-text-field>

        <v-card class="scroller" height="calc(100vh - 350px)" min-height="225" flat>
            <v-list rounded>
                <v-list-item-group
                    v-model="selected_reservoir"
                    color="primary"
                >
                    <v-list-item
                        v-for="(reservoir, i) in filtered_reservoirs"
                        :key="i"
                    >
                        <v-list-item-content>
                            <v-list-item-title>{{reservoir.name}}</v-list-item-title>
                        </v-list-item-content>
                    </v-list-item>
                </v-list-item-group>
            </v-list>
        </v-card>

        <v-divider class="mt-5"></v-divider>

        <v-row class="ma-0 mt-5" justify="center">
            <v-btn 
                rounded 
                color="primary secondary--text" 
                large 
                @click="$emit('contacts')"
            >
                <v-icon class="mr-2">fas fa-user-friends</v-icon>
                Contatos
            </v-btn>
        </v-row>
    </v-card>
</template>

<script>
import {mapActions, mapState} from 'vuex'

export default {
    data: () => ({
        search_resevoir_string: '',
        selected_reservoir: undefined,
        filtered_reservoirs: []
    }),
    computed: {
        ...mapState('data', ['reservoirs'])
    },
    watch: {
        reservoirs(){
            this.search_reservoir('')
        },
        selected_reservoir(){
            if(this.selected_reservoir != undefined){
                this.$emit('go_to_reservoir', this.reservoirs[this.selected_reservoir])
            }
            else{
                this.$emit('go_to_reservoir', undefined)
            }
        }
    },
    methods: {
        ...mapActions('data', ['get_reservoirs']),
        search_reservoir(search_resevoir_string){
            if(this.reservoirs.length > 0){
                this.filtered_reservoirs = this.reservoirs.filter(reservoir => reservoir.name.toLowerCase().includes(search_resevoir_string.toLowerCase()))
            }
        }
    },
    mounted(){
        this.get_reservoirs()
    }
}
</script>